*,
*::before,
*::after {
    box-sizing: border-box;
}

body {
    margin: 0;
    font-family: system-ui, -apple-system, BlinkMacSystemFont, "Segoe UI",
    sans-serif;
    background: linear-gradient(135deg, #111827, #020617);
    color: #e5e7eb;
}

.app {
    min-height: 100vh;
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 24px;
}

.card {
    width: 100%;
    max-width: 560px;
    background: rgba(15, 23, 42, 0.95);
    border-radius: 16px;
    padding: 24px 24px 28px;
    box-shadow: 0 18px 45px rgba(0, 0, 0, 0.45);
    border: 1px solid rgba(148, 163, 184, 0.2);
}

.title {
    margin: 0 0 8px;
    font-size: 24px;
    font-weight: 600;
    text-align: center;
}

.subtitle {
    margin: 0 0 20px;
    text-align: center;
    font-size: 14px;
    color: #9ca3af;
}

.section {
    margin-top: 16px;
}

.section-title {
    margin: 0 0 10px;
    font-size: 16px;
    font-weight: 500;
}

.password-row {
    display: flex;
    gap: 8px;
    margin-bottom: 10px;
}

.password-input {
    flex: 1;
    padding: 10px 12px;
    border-radius: 10px;
    border: 1px solid rgba(148, 163, 184, 0.4);
    background: #020617;
    color: #e5e7eb;
    font-size: 14px;
    outline: none;
}

.password-input::placeholder {
    color: #6b7280;
}

.password-input:focus {
    border-color: #38bdf8;
    box-shadow: 0 0 0 1px rgba(56, 189, 248, 0.4);
}

.field {
    margin-bottom: 18px;
}

.label {
    display: flex;
    justify-content: space-between;
    font-size: 14px;
    margin-bottom: 6px;
    color: #9ca3af;
}

.label span {
    color: #e5e7eb;
    font-weight: 500;
}

.field input[type="range"] {
    width: 100%;
}

.options {
    display: grid;
    grid-template-columns: 1fr;
    gap: 6px;
    margin-bottom: 12px;
}

.checkbox {
    display: flex;
    align-items: center;
    gap: 8px;
    font-size: 14px;
    color: #d1d5db;
}

.checkbox input {
    width: 16px;
    height: 16px;
}

.error {
    margin-bottom: 8px;
    font-size: 13px;
    color: #fca5a5;
}

.btn {
    border: none;
    border-radius: 10px;
    padding: 8px 14px;
    font-size: 14px;
    cursor: pointer;
    font-weight: 500;
    transition: transform 0.08s ease, box-shadow 0.08s ease,
    background 0.15s ease;
    white-space: nowrap;
}

.btn:active {
    transform: translateY(1px);
    box-shadow: none;
}

.btn.primary {
    width: 100%;
    margin-top: 8px;
    background: linear-gradient(135deg, #22c55e, #16a34a);
    color: white;
    box-shadow: 0 10px 25px rgba(34, 197, 94, 0.35);
}

.btn.primary:hover {
    background: linear-gradient(135deg, #4ade80, #22c55e);
}

.btn.secondary {
    background: #111827;
    color: #e5e7eb;
    border: 1px solid rgba(148, 163, 184, 0.4);
}

.btn.secondary:hover:not(:disabled) {
    background: #1f2937;
}

.btn.tertiary {
    background: transparent;
    color: #93c5fd;
    border: 1px solid rgba(147, 197, 253, 0.6);
}

.btn.tertiary:hover {
    background: rgba(30, 64, 175, 0.4);
}

.btn.small {
    padding: 4px 10px;
    font-size: 12px;
}

.btn:disabled {
    opacity: 0.6;
    cursor: default;
}

.history {
    margin-top: 18px;
}

.history-header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 8px;
}

.history-list {
    list-style: none;
    margin: 0;
    padding: 0;
    max-height: 220px;
    overflow-y: auto;
    border-radius: 10px;
    border: 1px solid rgba(148, 163, 184, 0.25);
    background: rgba(15, 23, 42, 0.7);
}

.history-item {
    display: flex;
    align-items: center;
    gap: 8px;
    padding: 6px 10px;
    font-size: 13px;
    border-bottom: 1px solid rgba(31, 41, 55, 0.8);
}

.history-item:last-child {
    border-bottom: none;
}

.history-password {
    flex: 1;
    font-family: "JetBrains Mono", ui-monospace, SFMono-Regular, Menlo, Monaco,
    Consolas, "Liberation Mono", "Courier New", monospace;
    word-break: break-all;
}

.history-meta {
    font-size: 11px;
    color: #9ca3af;
    min-width: 60px;
    text-align: right;
}